.calendarContainer {
    padding: 30px;
}

.heading {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
    color: #2B416C;
}


.closeButton {

    cursor: pointer;
    font: inherit;
    color: #181B34;
    background-color: transparent;
    padding: 10px;   
    font-size: 1rem;
    line-height: 19px;
    font-weight: bold; 
    font-family: Roboto;
    border: 2px solid #181B34;
    text-align: center;
    margin-top: 20px;       
}

.closeButtonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}
